.navbar{
    color: white;
    background-color: #030205;
    display: flex;
    align-items: center;
    padding: 1%;
    width: 100vw;
}

.navbar__search{
    align-items: center;
    display: flex;
    margin: 0% 0%;

}
.navbar__searchInput{
    width: 200px;
    border: 1px solid white;
    border-radius: 10px;
    color: white;
    background: black;
}


.navbar__searchInput::placeholder {
    color: white;
  }



  .navbar__button{
    background: linear-gradient(139deg, rgba(10, 251, 252, 1) 1%, rgba(12, 235, 244, 1) 9%, rgba(18, 192, 223, 1) 31%, rgba(27, 123, 190, 1) 88%, rgba(28, 117, 187, 1) 91%);
    color: #ffff;
    padding: 5px 10px;
    font-size: 0.8rem;
    border-radius: 10px;
    text-decoration: none;
    margin: 0% 4%;

  }

  .navbar__Walletbutton{
    background: linear-gradient(139deg, rgba(10, 251, 252, 1) 1%, rgba(12, 235, 244, 1) 9%, rgba(18, 192, 223, 1) 31%, rgba(27, 123, 190, 1) 88%, rgba(28, 117, 187, 1) 91%);
    color: #ffff;
    padding: 5px 10px;
    font-size: 0.8rem;
    border-radius: 10px;
    text-decoration: none;
    /* position: absolute;
    right: 6%; */
  }


  .navbar__selectOption{
    width: 150px;
    color: white;
    font-size: 20px;
    background-color: black;
  }



  .navbar__heading{
    margin: 0% 7%;
    font-size: 25px;
  }