.sidebarmenu{
    background-color: #030205;
    height: 100%;
    width: -moz-fit-content;
    /* width: 17vw; */
    width: 280px;
    /* position: fixed; */
}

.sidebarmenu__avtarImg{

    width: 60%;
}

.sidebarmenu__box{
    display: flex;
    align-items: center;
    margin-left: 10%;
    color: white;
}


.sidebarmenu__box__headings{
    margin-left: 2%;
}

.forList{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 10%;
    color: white;
}


.forList__ditails{
    display: flex;
}
.sidebarmenu__box__teamList__details{
    color: white;
    margin-left: 15%;
}


.sidebarmenu__signOutbtn{

    background: linear-gradient(139deg, rgba(10, 251, 252, 1) 1%, rgba(12, 235, 244, 1) 9%, rgba(18, 192, 223, 1) 31%, rgba(27, 123, 190, 1) 88%, rgba(28, 117, 187, 1) 91%);
        color: #ffff;
        padding: 5px 5px;
        font-size: 1.2rem;
        margin-bottom: 32px;
        border-radius: 0px 50px 50px 0px;
        text-decoration: none;
}
