.register{
    background: linear-gradient(180deg, #000000d1, #00000000,#000000c7);
    margin: auto;
    /* width: 100%; */
    display: flex;
    justify-content: center;
    height: -moz-fit-content;
    height: fit-content;
    flex-direction: column;
    text-align: center;
    align-items: center;
    width: 50%;
    margin-top: 6%;
    padding: 2%;
   
}


.register__logo{
    width: 20%;
    margin-top: -12%;
}

.register__headings{
    color: white;
}


.register__inputBox{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.register__inputBox__mainbox{
    display: flex;
}

.register__inputBox__left input{
    border: none;
        border-bottom: 1px solid white;
        background-color: inherit;
        width: 80%;
        margin-top: 20px;
        color: white;
}


.register__inputBox__left input::placeholder{
    color: white;
  }


  .register__inputBox__right{
    display: flex;
    flex-direction: column;
  }


  .register__inputBox__right select{
    border: none;
    border-bottom: 1px solid white;
    background-color: inherit;
    width: 80%;
    margin-top: 20px;
    color: white;
  }

.register__inputBox__right option{
    background-color: black;
}

  .register__inputBox__right input{
    border: none;
    border-bottom: 1px solid white;
    background-color: inherit;
    width: 80%;
    margin-top: 20px;
    color: white;
  }


  .register__inputBox__left input::placeholder{
    color: white;
  }

 

  .register__button__box__button{
    padding: 10px 40px;
    background-color: var(--secondary-color);
    margin-top: 25% !important;
    border: none;
    border-radius: 20px;
    background-color: #0fd6eb;
    margin: 10%;
    font-size: 15px;
    font-weight: 600;
  }