
header {
    width: 100%;
    height: 100px;
    background-color: transparent;
}

header .navbarr {
    max-width: 1240px;
    height: 100%;
    padding: 2% 10%;
    margin: auto;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    background-color:transparent;
    border-radius: 20px;
    
    
}
.navbarr h3{
    color:white;
    
}

header .logo {
    display: flex;
    align-items: center;
    width: 100%;
}

header .logo img {
    width:20%;
    height: auto;
}

.hamburger {
    display: none;
    position: absolute;
    top: 2rem;
    right: 1rem;
}

.nav-menu {
    display: flex;
    align-items: center;
    justify-self: start;
}

.nav-item {
    padding: 0 1.5rem;
}

.nav-link {
    color: #fff;
}
a{

text-decoration: none;
color:white;

}

@media screen and (max-width:960px) {
    .hamburger {
        display: block;
    }
    .nav-menu {
        position: fixed;
        left: -100%;
        flex-direction: column;
        justify-content: center;
        background-color: var(--darkgrey);
        width: 100%;
        height: 100%;
        text-align: center;
        transition: 0.4s;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-item {
        margin: 1.5rem 0;
        font-size: 1.8rem;
    }
}

.content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.homeMain .col-11 {
    display: flex;
    flex-direction: column;
    justify-content: start;
    padding-top:10%;
}

.homeMain .col-11 h1 {
    font-size: 72px;
    font-weight: 700;
    color: #fff;
}

.homeMain .col-11 p {
    line-height: 1.6;
    text-align: justify;
    margin-top: 0;
    margin-bottom: 10%;
    color: #FFFFFF;
    font-family: "Poppins", Sans-serif;
    font-size: 16px;
    font-weight: 400;    
}

.homeMain .used-by p {
    color: var(--lightgrey);
    font-size: 12px;
    margin: 2rem 0 1rem 0;
}



.homeMain .col-12 {
    padding: 1rem;
    /* height: 70vh; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: .8rem;
}

.homeMain .col-12 p {
    color: var(--grey);
}



