.login{
    background: linear-gradient(180deg, #000000d1, #00000000,#000000c7);
    margin: auto;
    /* width: 100%; */
    display: flex;
    justify-content: center;
    height: -moz-fit-content;
    height: fit-content;
    flex-direction: column;
    text-align: center;
    align-items: center;
    width: 50%;
    margin-top: 6%;
    padding: 2%;
}


.login__logo{
    width: 20%;
    margin-top: -12%;
}


.login__headings{
    color: white;
}


.login__inputs{
    display: flex;
    flex-direction: column;
    width: 70%;
    /* justify-content: flex-start; */
    align-items: center;
}


.login__inputs input{
    border: none;
        border-bottom: 1px solid white;
        background-color: inherit;
        width: 80%;
        margin-top: 20px;
        color: white;
}


.login__inputs input::placeholder{
    color: white;
  }


.login__buttons{
    margin-top: 2%;
}


.login__buttons__signin{
    padding: 10px 40px;
      background-color:#0fd6eb;
      margin-top: 10px;
      border: none;
      border-radius: 20px;
}

.login__buttons__donthaveaccount{
    color: white;
}

.login__buttons__signup{
    padding: 10px 40px;
      background-color:#0fd6eb;
      margin-top: 10px;
      border: none;
      border-radius: 20px;
}